import axios, { AxiosError } from 'axios';
import { Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { authService } from './authService';

// Configuración de la URL base según la plataforma
const isProduction = window.location.hostname === 'luisvanegascol.github.io';
const RAILWAY_URL = 'https://pingpong-backend-production.up.railway.app';

export const API_URL = isProduction 
  ? RAILWAY_URL 
  : Platform.select({
      android: 'http://10.0.2.2:3000', // Para el emulador de Android
      ios: 'http://localhost:3000',    // Para el simulador de iOS
      default: 'http://192.168.0.27:3000' // Para desarrollo web local
    });

console.log('🌐 API_URL configurada:', API_URL);
console.log('📱 Plataforma:', Platform.OS);
console.log('🏭 Entorno:', isProduction ? 'Producción' : 'Desarrollo');

// Crear instancia de axios con la configuración base
export const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json'
  },
  timeout: 10000, // 10 segundos de timeout
  validateStatus: function (status) {
    return status >= 200 && status < 500; // Aceptar todos los códigos de estado para manejarlos
  }
});

// Función para probar la conexión
export const testConnection = async () => {
  try {
    console.log('🔍 Probando conexión al servidor...');
    const response = await api.get('/test');
    console.log('✅ Conexión exitosa:', response.data);
    return response.data;
  } catch (error) {
    if (axios.isAxiosError(error)) {
      console.error('❌ Error al conectar con el servidor:', {
        message: error.message,
        code: error.code,
        response: error.response?.data,
        config: {
          url: error.config?.url,
          baseURL: error.config?.baseURL,
          method: error.config?.method
        }
      });
    } else {
      console.error('❌ Error desconocido:', error);
    }
    throw error;
  }
};

// Interceptor para agregar el token de autenticación
api.interceptors.request.use(
  async (config) => {
    try {
      console.log('🚀 Iniciando petición a:', config.url);
      console.log('🔧 Configuración:', {
        baseURL: config.baseURL,
        method: config.method,
        headers: config.headers
      });

      const token = await AsyncStorage.getItem('token');
      console.log('🔍 Token recuperado:', token ? 'Sí' : 'No');
      
      if (token) {
        config.headers['Authorization'] = `Bearer ${token}`;
        console.log('🔑 Token agregado a los headers');
      }

      return config;
    } catch (error) {
      console.error('❌ Error en interceptor de request:', error);
      return Promise.reject(error);
    }
  },
  (error: AxiosError) => {
    console.error('❌ Error en la configuración de la petición:', error);
    return Promise.reject(error);
  }
);

// Interceptor para manejar respuestas
api.interceptors.response.use(
  (response) => {
    console.log('✅ Respuesta exitosa:', {
      url: response.config.url,
      status: response.status,
      data: response.data
    });
    return response;
  },
  async (error: AxiosError) => {
    console.error('❌ Error en la respuesta:', {
      url: error.config?.url,
      status: error.response?.status,
      message: error.message,
      data: error.response?.data
    });

    if (error.code === 'ECONNABORTED') {
      console.error('⏰ Timeout en la petición');
    }

    if (error.response?.status === 403) {
      console.log('⚠️ Error 403 detectado, limpiando datos de autenticación...');
      await authService.logout();
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Definir los tipos de las respuestas esperadas
export interface Player {
  id: number;
  name: string;
  victories: number;
}

export interface Match {
  id: number;
  player1_id: number;
  player2_id: number;
  player1_result: string;
  player2_result: string;
  match_date: string;
  winner_id: number;
  player1_name?: string;
  player2_name?: string;
  winner_name?: string;
}

export interface Tournament {
  id_torneo: number;
  nombre: string;
  fecha_inicio: string;
  fecha_fin: string;
  estado: string;
}

export interface Ranking {
  id_rankings: number;
  id_torneo: number;
  id_jugador: number;
  posicion: number;
  partidas_ganadas: number;
  partidas_perdidas: number;
  nombre_jugador?: string;
  nombre_torneo?: string;
}

// Funciones para interactuar con la API
export const getPlayers = async (): Promise<Player[]> => {
  try {
    const response = await api.get('/players');
    return response.data;
  } catch (error) {
    console.error('Error al obtener jugadores:', error);
    throw error;
  }
};

export const addPlayer = async (name: string): Promise<Player> => {
  try {
    const response = await api.post('/players', { name });
    return response.data;
  } catch (error) {
    console.error('Error al agregar jugador:', error);
    throw error;
  }
};

export const addMatch = async (
  player1Id: number,
  player2Id: number,
  player1Result: string,
  player2Result: string
): Promise<Match> => {
  try {
    const requestBody = {
      player1_id: player1Id,
      player2_id: player2Id,
      player1_result: player1Result,
      player2_result: player2Result
    };

    console.log('🚀 Iniciando solicitud POST a /matches');
    console.log('📦 Datos a enviar:', requestBody);
    console.log('🔧 Configuración de la solicitud:', {
      url: api.defaults.baseURL + '/matches',
      headers: api.defaults.headers
    });

    const response = await api.post('/matches', requestBody);
    console.log('✅ Respuesta del servidor:', response.data);
    return response.data;
  } catch (error) {
    console.error('❌ Error al agregar partida:', error);
    if (axios.isAxiosError(error)) {
      console.error('Detalles del error:', {
        status: error.response?.status,
        statusText: error.response?.statusText,
        data: error.response?.data,
        config: {
          url: error.config?.url,
          method: error.config?.method,
          headers: error.config?.headers,
          baseURL: error.config?.baseURL
        }
      });
    }
    throw error;
  }
};

export const getMatches = async (): Promise<Match[]> => {
  try {
    const response = await api.get('/matches');
    return response.data;
  } catch (error) {
    console.error('Error al obtener partidas:', error);
    throw error;
  }
};

export const deletePlayer = async (id: number): Promise<void> => {
  try {
    console.log('🚀 Iniciando solicitud DELETE a:', `/players/${id}`);
    await api.delete(`/players/${id}`);
    console.log('✅ Jugador eliminado correctamente');
  } catch (error) {
    console.error('Error al eliminar jugador:', error);
    throw error;
  }
};

export const deleteMatch = async (id: number): Promise<void> => {
  try {
    await api.post(`/matches/${id}/delete`);
    console.log('✅ Partida eliminada correctamente');
  } catch (error) {
    console.error('Error al eliminar partida:', error);
    throw error;
  }
};

